.card_full:first-child {
  grid-area: 'main'; 
  background-position: 'center';
  background-size: 'cover';
  background-repeat: 'no-repeat';
  box-shadow: '0px 0px 13px 0px rgba(0,0,0,0.46)';
  padding-left: '25px';
  padding-right: '25px';
  position: relative;
  /* background-color: rgba(0,0,0,0.5); */
  /* background-image: url("https://www.fillmurray.com/640/360"); */
  display: flex;
  height: 300px;
}
.card_full:nth-child(1+n) {
  grid-area: 'sotck'; 
  background-position: 'center';
  background-size: 'cover';
  background-repeat: 'no-repeat';
  box-shadow: '0px 0px 13px 0px rgba(0,0,0,0.46)';
  padding-left: '25px';
  padding-right: '25px';
  position: relative;
  /* background-color: rgba(0,0,0,0.5); */
  /* background-image: url("https://www.fillmurray.com/640/360"); */
  display: flex;
  height: 150px;
}
.slider:nth-child(2) {

  background-position: 'center';
  background-size: 'cover';
  background-repeat: 'no-repeat';
  box-shadow: '0px 0px 13px 0px rgba(0,0,0,0.46)';
  padding-left: '25px';
  padding-right: '25px';
  position: relative;
  background: red;


  display: grid;
  height: 100%;
  width: 49%;
}

.card-body{

  display: inline-block;
  align-self: flex-end;
  padding-bottom: 25px;
}
.post_body{
  color: #FFFFFFFF;
  font-size: 1.2em;
  /* font-weight: 600; */
  height: auto;
}
.post_btn{
  background-color: var(--weirdBlue);
  color: var(--notQuiteWhite);
  font-size: 1.1em;
  font-weight: 550;
  padding: 13px 25px;
  border-radius: 7px;
  box-shadow: 0px 0px 7px 0px rgba(0,0,0,0.46);
  width: fit-content;
  /* height: 45px; */

}
@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

  

  .cardId {

    background-size: cover;
