:root{
  --weirdBlue: #0588A0;
  --notQuiteBlack: #3A414D;
  --notQuiteWhite: #CECECE;
}
.App {
  text-align: left;
}
body{
  background-color: #fbfbfb;
}
.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: var(--notQuiteWhit);
}

.App-link {
  color: var(--weirdBlue);
}